import React, {Component} from "react";
import {GoogleMap,withScriptjs,withGoogleMap,Marker,InfoWindow} from 'react-google-maps'
import axios from 'axios'
import Order from '../Orders/Order/Order'

class Map extends Component {
    state = {
        markers: [
            {}
        ],
        initLat: null,
        initLng: null,
        selectedMark: null
    }

    componentDidMount() {
        navigator.geolocation.getCurrentPosition(function (position) {
            console.log("Latitude is :", position.coords.latitude);
            console.log("Longitude is :", position.coords.longitude);
        });

        // this.setState({markers:[{lat:}]})
        // or you can set markers list somewhere else
        // please also set your correct lat & lng
        // you may only use 1 image for all markers, if then, remove the img_src attribute ^^
        axios.get('https://buy-the-way-a829f.firebaseio.com/orders.json')
            .then(response=>{
                let requests=[]
                // console.log(response.data)
                let index=0;
                for(let key in response.data){
                     // console.log(response.data[key], "reponse")
                     // console.log(groceries)
                    let request={
                        id:index++,
                         lat: response.data[key].lat,
                        lng: response.data[key].lng,
                        firstName:response.data[key].firstName,
                        lastName:response.data[key].lastName,
                        reason:response.data[key].reason,
                        address:response.data[key].address,
                        phoneNumber:response.data[key].phoneNumber.value,
                        items:response.data[key].items,
                        comment:response.data[key].comment,
                        image:response.data[key].imageURL,

                       }
                    requests.push(request)
                     console.log(request)
                }

                 this.setState({markers:requests})
                // console.log(this.state.markers)
                //  console.log(this.state.markers)
            })
        axios.get("gs://buy-the-way-a829f.appspot.com")
            .then(response=>{
                // console.log(response, 'photo')
            })
    }
    viewFullOrder=(id)=>{
        console.log("asdasd")
        console.log(id)
        //this.props.history.push('/card/'+id)
    }
    render() {
        return (
            <div>
                <GoogleMap
                    defaultZoom={15}
                    defaultCenter={{lat: 31.243870, lng: 34.793991}}
                >
                    {this.state.markers.map((place, i) => (
                        <Marker
                            key={i}
                            position={{
                                lat: place.lat,
                                lng: place.lng,
                            }}
                            onClick={() => {
                                this.setState({selectedMark: place})
                                console.log(this.state.selectedMark)
                            }}
                            // icon={{
                            //     url:icon,
                            //     scaledSize: new window.google.maps.Size(25,25)
                            // }}
                        />
                    ))}
                    {this.state.selectedMark && (
                        <InfoWindow
                            onCloseClick={() => {
                                this.setState({selectedMark: null});

                            }}
                            position={{
                                lat: this.state.selectedMark.lat,
                                lng: this.state.selectedMark.lng
                            }}
                        >
                            <div>
                                <Order
                                    image={this.state.selectedMark.image}
                                    fullName={this.state.selectedMark.firstName.value+" "+this.state.selectedMark.lastName.value}
                                    phoneNumber={this.state.selectedMark.phoneNumber}
                                    groceries={this.state.selectedMark.items}
                                    address={this.state.selectedMark.address}
                                    reason={this.state.selectedMark.reason}
                                    comment={this.state.selectedMark.comment}
                                    />
                            </div>
                        </InfoWindow>
                    )}
                </GoogleMap>
            </div>
        );
    }
}


const MapWrapped = withScriptjs(withGoogleMap(Map))

export default function MyMap() {
        return(
            <div style={{width:'100vw', height:'95vh'}}>
                <MapWrapped googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=${
                    "AIzaSyCz75H6TUotNy-TcGON0wmw5pjOM2quK6s"
                }`}
                            loadingElement={<div style={{height:"100%"}}/>}
                            containerElement={<div style={{height:"100%"}}/>}
                            mapElement={<div style={{height:"100%"}}/>}

                />
            </div>
        )
}

