import React, {Component} from "react";
import {Button, Container,Col, Image} from 'react-bootstrap'
import avatar from '../../../assets/avatar.jpg'

class ProfilePage extends Component{
    state={
        hasRequest:false
    }
    goToOrderForm=()=>{
        this.props.history.push('/OrderPage')
    }
    render() {
        let button=null
        let button2=null
        if(this.state.hasRequest){
            button=<Button variant="primary">Edit request</Button>
            button2=<Button variant="primary" >Remove request</Button>
        }
        else{
            button=<Button variant="primary" onClick={this.goToOrderForm}>Enter request</Button>
        }
        return(
            <div>
                <Image src={avatar} roundedCircle />
                {button}
                {button2}
            </div>
        )
    }

}
export default ProfilePage